export function currencySymbol(currency) {
    switch (currency) {
        case "gbp": return "£";
        case "eur": return "€";
        case "usd": return "$";
        case "aud": return "A$";
        case "sgd": return "S$";
        case "mxn": return "Mex$";
        case "cop": return "$";
    }
}
export function currencyLabel(currency) {
    switch (currency) {
        case "gbp": return "British Pound - GBP";
        case "eur": return "Euro - EUR";
        case "usd": return "US Dollar - USD";
        case "aud": return "Australian Dollar - AUD";
        case "sgd": return "Singapore Dollar - SGD";
        case "mxn": return "Peso Mexicano - MXN";
        case "cop": return "Peso Colombiano  - COP";
    }
}
/**
 * Converts an ISO Country code to the currency they use.
 * https://en.wikipedia.org/wiki/List_of_ISO_3166_country_codes
 * Or http://country.io/names.json
 */
export function countryCodeToCurrency(country) {
    switch (country) {
        case "GB":
            return "gbp";
        case "AT":
        case "BE":
        case "CY":
        case "EE":
        case "FI":
        case "FR":
        case "DE":
        case "GR":
        case "IE":
        case "IT":
        case "LV":
        case "LT":
        case "LU":
        case "MT":
        case "NL":
        case "PT":
        case "SK":
        case "SI":
        case "ES":
            return "eur";
        case "AU":
            return "aud";
        case "SG":
            return "sgd";
        case "MX":
            return "mxn";
        case "CO":
            return "cop";
        default:
            return "usd";
    }
}
