        mirrorX(graphic);
        graphic.close()
            .withStroke("thinner", COLOR)
            .withGradientFill("right-down", [
            ["#e1f6fc80", 0.3],
            ["#ffffff80", 0.5],
            ["#e1f6fc80", 0.7],
        ]);
