import { P, R, Segments, SharedColors } from "apparatus/library/common";
import { Group, Path } from "paper";
var BAFFLE_COLOR = "#999999";
var FumeHood = /** @class */ (function () {
    function FumeHood() {
    }
    FumeHood.prototype.render = function (appearance) {
        var strokeThickness = 10.0;
        var sashHeight = 600;
        var sashSlide = appearance.sashOpening * 0.95 * sashHeight;
        var fumeX = 430;
        var outer1 = new Path(Segments([[0, 620]], [[-5, 620]], [[-30, 580]], [[-30, 0]], [[fumeX, 0]], [[fumeX, -100]])).withStroke(strokeThickness, SharedColors.stroke);
        outer1.strokeCap = "butt";
        var outer2 = new Path(Segments([[fumeX + 80, -100]], [[fumeX + 80, 0]], [[600, 0]], [[600, 1200]])).withStroke(strokeThickness, SharedColors.stroke);
        var baffle1 = new Path.Line(P(100, 20), P(500, 350)).withStroke("thick", BAFFLE_COLOR);
        var baffle2 = new Path.Line(P(550, 400), P(550, 750)).withStroke("thick", BAFFLE_COLOR);
        var baffle3 = new Path.Line(P(550, 800), P(550, 1150)).withStroke("thick", BAFFLE_COLOR);
        var sash = new Path.Rectangle(R(1, 1200 - sashHeight - strokeThickness / 2, 10, sashHeight), 3.0)
            .withStroke("thinner", "#56acd1").withFill("#7bcbed50");
        sash.position.y -= sashSlide;
        var workbench = new Path.Rectangle(R(0, 1200, 600, 200))
            .withStroke(strokeThickness, SharedColors.stroke)
            .withFill("#777777");
        var fumeHood = new Group([outer1, outer2, baffle1, baffle2, baffle3, sash, workbench]);
        var graphic;
        switch (appearance.display) {
            case "full":
                graphic = fumeHood;
                break;
            case "clipped":
                var mask = new Path.Rectangle(R(-50, 500, 700, 800));
                var faders = mask.clone();
                mask.withFill("black");
                faders.setGradientFill("down", [
                    ["#ffffffff", 0.0],
                    ["#ffffff00", 0.05],
                    ["#ffffff00", 0.95],
                    ["#ffffffff", 1.0],
                ]);
                graphic = new Group([mask, fumeHood, faders]);
                graphic.clipped = true;
        }
        return {
            graphic: graphic,
            hitShape: graphic.bounds.toShape(),
            pivot: fumeHood.bounds.center,
            snapping: [
                { type: "placeable_surface", start: workbench.strokeBounds.topLeft, end: workbench.strokeBounds.topRight }
            ]
        };
    };
    FumeHood.properties = {
        label: "Fume Hood",
        canContainLiquids: false,
        flippable: true,
        defaultAppearance: {
            sashOpening: 0.5,
            display: "clipped"
        },
        appearanceSpecs: [
            {
                spec: "radio", key: "display", label: "Display",
                options: [
                    { value: "full", label: "Full" },
                    { value: "clipped", label: "Clipped" },
                ],
            },
            {
                spec: "slider", key: "sashOpening", label: "Sash Opening",
                min: 0.0, max: 1.0, step: 0.01,
            }
        ],
    };
    return FumeHood;
}());
export { FumeHood };
