import { mirrorX } from "apparatus/draw";
import { P, Pivot, R, Segments, SharedColors } from "apparatus/library/common";
import { CompoundPath, Group, Path, Shape } from "paper";
var SLIT_PLATE_COLOR = "#555555";
var RayBox = /** @class */ (function () {
    function RayBox() {
    }
    RayBox.prototype.render = function (appearance) {
        var slits = appearance.slits;
        var slitGap = 10;
        var box = new Path(Segments([[30, 0]], [[30, -80]], [[35, -80]], [[35, 60]])).withStroke("default").withFill("#444444").close();
        mirrorX(box);
        var graphic = new Group([box]);
        if (slits !== "none") {
            var slitPlate = new CompoundPath([]).withStroke("default", SLIT_PLATE_COLOR);
            slitPlate.moveTo(P(-30, -65));
            for (var i = 0; i < slits; i++) {
                var dx = ((slits - 1) / -2 + i) * slitGap;
                slitPlate.lineTo(P(dx - 1, -65));
                slitPlate.moveBy(P(2, 0));
            }
            slitPlate.lineTo(P(30, -65));
            graphic.insertChild(0, slitPlate);
        }
        var rayThickness = 2.0;
        var rayLength = 200;
        if (appearance.light === "front") {
            // First draw the glow.
            if (slits === "none") {
                graphic.insertChild(0, Shape.Rectangle(R(0, 0, 60, 200, Pivot.BOTTOM_CENTER)).withGradientFill("up", [["#da1414aa", 0.5], ["#ff000000", 1.0]]));
            }
            else {
                graphic.insertChild(0, Shape.Rectangle(R(0, 0, 60, 65, Pivot.BOTTOM_CENTER)).setRadialGradientFill([["#da1414aa", 0.5], ["#da141440", 1.0]], P(0, 0), P(30, -65)));
                // Draw rays
                for (var i = 0; i < slits; i++) {
                    var dx = ((slits - 1) / -2 + i) * slitGap;
                    graphic.insertChild(0, new Path.Line(P(dx, -64), P(dx, -(64 + rayLength))).withStroke(rayThickness, SharedColors.ray));
                }
            }
        }
        return {
            graphic: graphic,
            pivot: box.bounds.center,
            hitShape: box.bounds.toShape()
        };
    };
    RayBox.properties = {
        label: "Ray Box",
        canContainLiquids: false,
        defaultAppearance: {
            light: "off",
            slits: "none"
        },
        appearanceSpecs: [
            {
                spec: "toggle-buttons",
                size: "text",
                label: "Light",
                key: "light",
                options: [
                    { value: "off", label: "Off", icon: "" },
                    { value: "front", label: "On", icon: "" },
                ]
            },
            {
                spec: "toggle-buttons",
                size: "text",
                label: "Slits",
                key: "slits",
                options: [
                    { value: "none", label: "None", icon: "" },
                    { value: 1, label: "1", icon: "" },
                    { value: 3, label: "3", icon: "" },
                ]
            },
        ],
    };
    return RayBox;
}());
export { RayBox };
