/** App entry point. */
function main() {
    // Set up Toastr options.
    setUpToastr();
    // Polyfill fetch for IE11.
    if (window.fetch === undefined) {
        clog("Polyfilling fetch...");
        window.fetch = window['unfetch'];
    }
    // Initialise app.
    // Put the Provider on the outside and then place the connected app inside it.
    var app = React.createElement(ConnectedApp);
    var provider = React.createElement(Provider, { store: appStore }, app);
    DOM.render(provider, document.getElementById("app"));
    // TODO: Handle case where the store is empty to begin with
    // Add items to start with.
    if (!Consts.isDev) {
        // Place first items slightly off center so that new items
        // added by the user are visible.
        var center = paper.project.view.center.add(P(-50, -30));
        var beaker = createApparatus("beaker", center);
        beaker.selected = true;
        wrappedAppStore.dispatch(addItems([
            createApparatus("table", center.add(P(0, 72))),
            beaker
        ]));
        wrappedAppStore.dispatchFn("addArrow", addArrowMutator([__assign(__assign({}, Arrow.defaultArrow), { id: ID.mint("arrow"), start: center.subtract(P(120, 90)).toPlain(), end: center.subtract(P(60, 20)).toPlain(), isChemMarkEnabled: true, label: "H2O" })]));
        registerUnloadListener();
    }
    else {
        var toAdd = [
            "__live edit__",
            "__raster image__",
            "powerpack",
            "spring",
            "mirror",
            "magnet",
            "voltmeter-s",
            "ammeter-s",
            "pulley",
            "cable",
            "glass block",
            "slotted weight",
            "raybox",
            "choice chamber",
            "vernier",
            "digital voltmeter",
            "digital ammeter",
            "solenoid",
            "centrifuge",
            "weight boat",
            "paperclip",
            "trolley",
            "ramp",
            "light gate",
            "tlc chamber",
        ];
        appStore.dispatch(addItems(toAdd.map(function (item, i) { return createApparatus(item, { x: (i % 4) * 150 + 100, y: Math.floor(i / 4) * 150 + 150 }); })));
    }
    registerBootstrapColorpickerExtensions();
    // Fix mouse up outside bootstrap colorpicker not ending slider drag.
    $("body").on("mouseup", function () {
        $("" + BOOTSTRAP_COLORPICKER_CSS_SELECTOR).each(function (i, el) {
            var data = $(el).data();
            if (!data)
                return;
            if (!data.colorpicker)
                return;
            data.colorpicker.sliderHandler.released();
        });
    });
}
