// Shared ui elements for Promo popup
import * as React from "react";
export var Tick = function () { return (React.createElement("i", { className: "fa fa-check" })); };
export var Info = function (props) { return (React.createElement("i", { className: "info_chip fa fa-info-circle ml-1", title: props.hint })); };
export var InfoLink = function (props) { return (React.createElement("a", { href: props.link, target: "_blank" },
    React.createElement(Info, { hint: props.hint }))); };
export var BeaconLink = function (props) { return (React.createElement("a", { className: "info_chip fa fa-info-circle ml-1", "data-beacon-article-sidebar": props.article, title: props.hint })); };
